/*
  Declare variables for font color and text sizes
*/
:root {
  --font-color: #030303;
  --inverse-font-color: #f3f3f3;
  --background-color: #fcfcfc;
  --nav-background-color: #ffffff;
  --input-background-color: #fcfcfc;
  --input-border-color: #999;
  --input-text-color: #030303;
  --error-color: red;
  --error-bg: #ff000011;
  --accent-color: #4b4486;
  --accent-color-half-transparent: #4b448644;
  --accent-color-quarter-transparent: #4b448622;
  --accent-color-transparent: #4b448600;
  --accent-color-light: #aba4d6a0;
  --accent-color-lighter: #aba4d640;
  --h1-font-size: clamp(24px, 4.4vmin, 32px);
  --h2-font-size: clamp(21px, 3.8vmin, 26px);
  --h3-font-size: clamp(18px, 3vmin, 22px);
  --body-text-size: clamp(14px, 2.6vmin, 16px);
  --default-font-weight: 200;
  --button-background-color: #ffffff;
  --button-border-color: #322B6D;
  --article-bg-color-primary: #aba4d660;
  --article-bg-color-secondary: #aba4d630;
}
/*
  reset
*/
body {
  margin: 0;
  padding: 0 0 0 0;
  min-height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  background-color: #fcfcfc;
  background-color: var(--background-color);
}
/* 
  default main styling
*/
main {
  display: grid;
  grid-row-gap: clamp(14px, 2.6vmin, 16px);
  row-gap: clamp(14px, 2.6vmin, 16px);
  grid-row-gap: var(--body-text-size);
  row-gap: var(--body-text-size);
  width: min(84vw, 900px);
  margin: 0 auto;
  padding-top: calc(clamp(24px, 4.4vmin, 32px) / 2);
  padding-top: calc(var(--h1-font-size) / 2);
  padding-bottom: 80px;
}
main > div {
  margin: clamp(14px, 2.6vmin, 16px) 0;
  margin: var(--body-text-size) 0;
}
/* list style */
ul, ol {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
/*
  general image styling
*/
img {
  width: 100%;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-filter: drop-shadow(
    0px 0px 4px #aba4d6a0);
          filter: drop-shadow(
    0px 0px 4px #aba4d6a0);
  -webkit-filter: drop-shadow(
    0px 0px 4px var(--accent-color-light)
  );
          filter: drop-shadow(
    0px 0px 4px var(--accent-color-light)
  );
}
/* 
  button styling
*/
button, .button-styling {
  background-color: #ffffff;
  background-color: var(--button-background-color);
  font-family: 'Montserrat', sans-serif;
  font-size: clamp(14px, 2.6vmin, 16px);
  font-size: var(--body-text-size);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  opacity: 0.7;
  cursor: pointer;
  display: inline-block;
  padding: 5px;
  max-width: 30ch;
  height: calc(clamp(14px, 2.6vmin, 16px) * 1.5 + 10);
  height: calc(var(--body-text-size) * 1.5 + 10);
  margin-block: clamp(14px, 2.6vmin, 16px);
  margin-block: var(--body-text-size);
  border: 1px solid #322B6D;
  border: 1px solid var(--button-border-color);
  border-radius: 5px;
  color: var(--button-text-color);
  font-weight: 200;
  transition: 0.3s;
  box-shadow: 1px 2px 4px #aba4d6a0;
  box-shadow: 1px 2px 4px var(--accent-color-light);
}
@media (hover: hover) {
  button:hover, .button-styling:hover {
    opacity: 1;
    color: #ffffff;
    color: var(--button-background-color);
    background-color: #322B6D;
    background-color: var(--button-border-color);
  }
}
button:first-of-type:not(:last-of-type), button + button:not(:last-of-type) {
  margin-right: 0;
  border-right: 1px solid #322B6D;
  border-right: 1px solid var(--button-border-color);
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
button + button {
  margin-left: 0;
  border-left: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.fake-button {
  margin-top: clamp(21px, 3.8vmin, 26px);
  margin-top: var(--h2-font-size);
  margin-bottom: clamp(21px, 3.8vmin, 26px);
  margin-bottom: var(--h2-font-size); 
  text-decoration: underline;
  text-align: center;
  cursor: pointer;
}
/* basic input styling */
input {
  background-color: #fcfcfc;
  background-color: var(--input-background-color);
  color: #030303;
  color: var(--input-text-color);
  border : 1px solid #999;
  border : 1px solid var(--input-border-color);
  padding: 5px;
  border-radius: 7px;
  outline: 0;
}
input:focus {
  border-color: #030303;
  border-color: var(--input-text-color);
}
input::-webkit-input-placeholder {
  color: #999;
  color: var(--input-border-color);
}
input:-ms-input-placeholder {
  color: #999;
  color: var(--input-border-color);
}
input::-ms-input-placeholder {
  color: #999;
  color: var(--input-border-color);
}
input::placeholder {
  color: #999;
  color: var(--input-border-color);
}
.inline-input > input {
  border: 0;
  outline: none;
  margin: 0 0 0 calc(1.5 * clamp(14px, 2.6vmin, 16px));
  margin: 0 0 0 calc(1.5 * var(--body-text-size));
  width: 14ch;
}
.inline-input > input[type="tel"] {
  width: 5ch;
}
.inline-input::before {
  content: "";
  box-shadow: none;
  outline: none;
  position: absolute;
  transform: translate(calc(clamp(14px, 2.6vmin, 16px) * .75), calc(clamp(14px, 2.6vmin, 16px) / 2));
  transform: translate(calc(var(--body-text-size) * .75), calc(var(--body-text-size) / 2));
  width: calc(clamp(14px, 2.6vmin, 16px) * .75);
  width: calc(var(--body-text-size) * .75);
  height: calc(clamp(14px, 2.6vmin, 16px) * .75);
  height: calc(var(--body-text-size) * .75);
  background-size: cover;
  background-image: url(data:image/png;base64,);
  -webkit-filter: drop-shadow(
    0px 0px 2px #ffffff
  );
          filter: drop-shadow(
    0px 0px 2px #ffffff
  );
}
