/* 
  general header and heading style
*/
header {
  width: 100%;
  display: grid;
  align-items: center;
  justify-items: center;
  text-align: center;
}
/* 
  Concept:
    h[1-3] are desktop
    h[4-6] are mobile
    Same font-size and color, but uses media query to change display
*/
h1, h2, h3, h4, h5, h6 {
  display: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0;
}
