  h1.long {
    display: block;
    text-align: center;
    font-size: var(--h1-font-size);
    font-weight: calc(var(--default-font-weight) + 100);
    text-decoration: underline;
  }
  h2.long {
    display: block;
    text-align: center;
    font-size: var(--h2-font-size);
  }
  h3.long {
    display: block;
    text-align: center;
    font-size: var(--h3-font-size);
  }
}
@media (max-width: 1099px) {
  h1.short {
    display: block;
    text-align: center;
